.fighters-page {
    padding: 20px;
  }
  
  .filter {
    margin-bottom: 20px;
  }
  
  input {
    padding: 10px;
    margin-left: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 200px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  th, td {
    padding: 10px;
    text-align: left;
    border: 1px solid #ddd;
  }
  
  th {
    background-color: #f4f4f4;
  }
  
  tr:hover {
    background-color: #f1f1f1;
  }

  /* FightersPage.css / AddFighter.css */

/* Kontener strony */
.fighters-page, .add-fighter-page {
    position: relative; /* Ustawiamy pozycjonowanie na relative, żeby przycisk był pozycjonowany względem tego kontenera */
    padding: 20px;
  }
  
  /* Stylizacja przycisku "Wróć" */
  .btn-back {
    position: absolute; /* Pozycjonowanie absolutne */
    top: 20px; /* Odległość od góry */
    right: 20px; /* Odległość od prawej krawędzi */
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s ease;
    border-radius: 5px;
  }
  
  .btn-back:hover {
    background-color: #0056b3;
  }
  