/* StartPage.css */

.start-page {
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), /* Ciemny gradient */
      url('images/background.jpg'); /* Ścieżka do zdjęcia */
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
  }
  
  
  .start-content {
    padding: 20px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  }
  
  h1   
  {
    
    font-size: 36px;
    margin-bottom: 20px;
  }
  
  p {
    font-size: 18px;
    margin-bottom: 40px;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .btn {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 18px;
    transition: background-color 0.3s ease;
    border-radius: 5px;
  }

  body {
  font-family: 'Poppins', sans-serif; /* Zmieniamy czcionkę na Poppins */
}

.start-page h1 {
  font-family: 'Poppins', sans-serif; /* Zmieniamy czcionkę dla nagłówków */
  font-size: 3rem;
  font-weight: bold;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8); /* Delikatny cień */
}

.start-page p {
  font-family: 'Poppins', sans-serif;
  font-size: 1.5rem;
}

.start-page .btn {
  font-family: 'Poppins', sans-serif;
  font-size: 1.2rem;
  padding: 12px 30px;
}

body {
    font-family: 'Fira Sans', sans-serif; /* Zmieniamy czcionkę na Fira Sans */
  }
  
  .start-page h1 {
    font-family: 'Fira Sans', sans-serif; /* Zmieniamy czcionkę dla nagłówków */
    font-size: 3rem;
    font-weight: 600; /* Możesz dostosować wagę czcionki */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
  }
  
  .start-page p {
    font-family: 'Fira Sans', sans-serif;
    font-size: 1.5rem;
  }
  
  .start-page .btn {
    font-family: 'Fira Sans', sans-serif;
    font-size: 1.2rem;
    padding: 12px 30px;
    transition: all 0.3s ease;
  }
  
  .start-page .btn:hover {
    background-color: #0056b3;
    transform: scale(1.15); /* Powiększenie przy najechaniu */
  }

 
  
  .btn:hover {
    background-color: #0056b3;
  }
  